.person-box{
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 3px 6px transparentize(color("black"), .6);
    overflow: hidden;
    img{
        width: 100%;
        height: 300px;
        object-fit: cover;
        object-position: center;
    }
    .person-body{
        padding: 20px;
    }
    .person-name{
        font-size: 24px;
        font-weight: lighter;
        text-align: center;
        margin: 0px;
    }
    ul.social{
        list-style: none;
        padding: 0px;
        margin: 20px 0px 0px 0px;
        color: color("dark");
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        li{
            &:not(:last-child){margin-right: 20px;}
            font-size: 30px;
            i{
                color: color("dark");
                &.twitter:hover{color: #1da1f2;}
                &.linkedin:hover{color: #007bb5;}
                &.facebook:hover{color: #1877f2;}
            }
        }
    }
}