.footer{
    padding: 50px 0px;
    margin-top: 100px;
    .footer-heading{
        font-size: 24px;
        font-weight: lighter;
        margin: 0px 0px 20px 0px;   
    }
    .footer-paragraph{
        font-size: 16px;
        font-weight: 500;
        line-height: 28px;
    }
    .footer-list{
        list-style: none;
        padding: 0px;
        margin: 50px 0px 0px 0px;
    }
    .footer-list:not(.social){
        li:not(:last-child){
            margin-bottom: 20px;
        }
        li{
            font-size: 18px;
            font-weight: lighter;
            &, & a{
                color: color("white");
                opacity: .8;
            }
            &:hover, & a:hover{
                opacity: 1;
            }
        }
    }
    .footer-list.social{
        display: flex;
        font-size: 24px;
        li:not(:last-child){
            margin-right: 30px;
        }
        .twitter,
        .linkedin,
        .insta,
        .facebook{
            cursor: pointer;
            color: color("white");
            transition: color .2 ease-in-out;
        }

        .twitter:hover{color: #1da1f2;}
        .linkedin:hover{color: #007bb5;}
        .insta:hover{color: #c32aa3;}
        .facebook:hover{color: #1877f2;}

    }
}