.hero-section{
    height: 100vh;
    min-height: 500px;
    color: white;
    padding: 40px 0px;
    background-color: color("light-blue");
    text-align: center;
    @include md{
        text-align: left;
        padding: 0px;
        background-color: transparent;
    }
    @include sm-r{
        .row{
            flex-direction: column-reverse;
        }
    }
    .col{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .text-container{
        height: 100vh;
    }
    @include sm-r{
        .text-container{
            height: auto;
            padding: 50px 0px;
        }
    }
    .hero-header{
        color: white;
        font-weight: bolder;
        font-size: 42px;
        margin-bottom: 0px;
        text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.6);
        @include sm-r{
            font-size: 24px;
        }
    }
    .hero-paragraph{
        font-size: 22px;
        @include sm-r{
            font-size: 20px;
        }   
    }
    .path{
        width: 100vw;
        position: absolute;
        top: 0px;
        z-index: 1;
        display: none;
        @include sm{
            display: block;
        }
    }
    .container{
        position: relative;
        z-index: 2;
    }
    .laptop{
        width: 100%;
    }
    .buttons{
        font-size: 30px;
        margin-top: 20px;
        & > * {
            display: inline-block;
            &:not(:last-child){
                margin-right: 30px;   
            }
        }
        .login{
            background-color: color("white");
            color: color("blue");
            padding: 5px 20px;
            border-radius: 3px;
            box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2);
            transition: .3s ease-in-out;
            &:hover{
                box-shadow: none;
            }
        }
        .register{
            color: color("white");
            transition: .3s ease-in-out;
            &:hover{
                text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.6);
            }
        }
    }
}
.control-section{
    position: relative;
    z-index: 2;
}
.control-box{
    padding: 60px 20px 30px 20px;
    margin-top: 60px;
    background-color: color("white");
    border-radius: 10px;
    box-shadow: 0px 3px 6px transparentize(color("black"), .7);
    font-size: 14px;
    font-weight: bold;
    text-align: center;
    position: relative;
    height: calc(100% - 60px);
    .control-icon{
        $size: 90px;
        height: $size;
        width: $size;
        line-height: $size;
        position: absolute;
        top: 0px;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: white;
        border-radius: 50%;
        box-shadow: 0px 3px 6px transparentize(color("black"), .7);
        font-size: 40px;
        i{
            color: color("blue");
        }
    }

    .control-heading{
        margin: 0px;
        font-size: 20px;
    }
    .control-paragraph{
        margin: 10px 0px 0px 0px;
        font-size: 14px;
        font-weight: 500;
    }
}

.how-section{
    margin: 50px 0px 150px 0px;
    position: relative;
    z-index: 2;
}

.how{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    $padding: 20px;
    padding: $padding;
    position: relative;
    @include md{
        align-items: center;
    }

    // ===== Line =====
    &:after{
        content: "";
        position: absolute;
        top: 50%;
        left: 60px;
        @include md{
            left: 50%;
        }
        transform: translate(-50%, -50%,);
        $width: 10px;
        width: $width;
        height: calc(100% - 50px);
        border-radius: $width;
        background-color: color("blue");
        z-index: 1;
    }

    .how-box{
        &:not(:first-child){ margin-top: 200px; }
        @include sm-r{
            &:not(:first-child){ margin-top: 300px; }
        }
        color: white;
        text-align: center;
        position: relative;
        z-index: 2;
        .how-number{
            $size: 80px;
            width: $size;
            height: $size;
            line-height: $size;
            font-size: 32px;
            color: color("blue");
            background-color: color("white");
            border: 4px solid color("blue");
            border-radius: 50%;
        }
        // ============= Body =============
        .how-body{
            width: calc(100vw - 218px);
            @include md{
                width: 35vw;
            }
            position: absolute;
            top: 0px;
            background-color: color("light-blue");
            border-radius: 10px;
            padding: 20px;
            @include sm-r{
                padding: 10px;
                width: 60vw !important;
                width: calc(100vw - 150px);
            }
        }

        &:nth-child(odd){
            .how-body{
                left: calc(100% + 20px);
                @include sm-r{
                    left: calc(100% + 10px);
                }
            }
        }
        &:nth-child(even){
            .how-body{
                left: calc(100% + 20px);
                @include md{
                    right: calc(100% + 20px);
                    left: auto;
                }
            }
        }
        
        .how-heading{
            font-size: 24px;
            @include sm-r{
                font-size: 18px;
            }
            margin: 0px;
            span::after{
                background-color: white;
                height: 5px
            }
        }
        .how-paragraph{
            font-size: 16px;
            font-weight: bold;
            @include sm-r{
                font-size: 14px;
            }

        }
    }
}
