*{
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}

body{
    padding: 0px;
    margin: 0px;
    background-color: color("light");
    font-family: 'Nunito', sans-serif;
    font-weight: bold;
    padding-top: 60px; // For the navbar
    overflow-x: hidden;
}

.img-fluid{
    width: 100%;
}

main{
    min-height: calc(100vh - 538px);
}
.section-title{
    font-size: 24px;
    color: color("blue");
    margin: 0px;
}
.page-paragraph{
    font-size: 20px;
    margin: 20px 0px;
    text-align: justify;
}
.paragraph-section{
    margin-top: 20px;
}
.paragraph-section{
    .page-paragraph{
        font-size: 18px;
        margin: 10px 0px 10px 30px;
    }
}
.small-heading{
    color: color("blue");
    font-size: 18px;
    margin: 0px;
}